.card {
  position: relative;
  /* border: 1px white solid; */
  border-radius: 15px;
  padding: 1rem 1rem 0 1rem;
  margin: 0;
  overflow-y: auto;
  overflow-x: hidden;
  background: rgba(0, 0, 0, 0.25);
  /* height: 25vh; */
  font-size: 1.5vw;
  height: 20vmin;
  box-shadow: 0 0 3px 3px rgba(0, 0, 0, 0.35);
}

.cardHeader {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: calc(2vh + 1rem);
  padding: 0 0 0.5rem 0;
  border-bottom: 1px white solid;
}

.cardTitle {
  padding: 0;
  margin: 0 0.5rem 0 0;
}

.modalPlacementHelper {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1000;
  height: 100%;
  width: 100%;
  border-radius: 15px;
}

.cardInner {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 1rem 0 0 0;
  height: 10vmin;
}

.attributeGrid {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  column-gap: 2vmin;
  row-gap: 2vmin;
  margin-top: 2vmin;
}

.cardButtonContainer__One {
  padding: 0;
}

@media (max-width: 799px) and (orientation: landscape) {
  .card {
    min-height: 32vh;
  }
  .cardTitle {
    font-size: 2.5vw;
  }
  .attributeRow > li,
  .attributeRow2 > li {
    margin: 0 0.2rem;
    font-size: 2vw;
  }
}

